@import url(https://rsms.me/inter/inter-ui.css);
body {
  margin: 0;
  padding: 0;
  font-family: InterUI, "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.color-primary      { color: #AA3939 }  /* Primary */
.color-light        { color: #FFAAAA }  /* Light */
.color-medium-light { color: #D46A6A } /* Medium-light */
.color-medium-dark  { color: #801515 } /* Medium-dark */
.color-dark         { color: #550000 } /* Dark */

.App {
  text-align: center;
}

.App-link:link, .App-link:visited {
  color: #FFAAAA;
}

.App-link:hover, .App-link:active {
  color: #801515;
}

.App-logo {
  font-weight: bolder;
  font-size: calc(40px + 2vmin);
}

.App-header, .App-content, .App-footer, .App-bibliography {
  padding: 50px 150px;
  background-color: #AA3939;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-input-section {
  max-width: 75vw;
  display: flex;
  flex-direction: column;
}

